import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { DownloadRegQryBean } from './bean/downloadRegQry.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';

import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'downCenter',
    templateUrl: './downCenter.component.html',
    styleUrls: ['./downCenter.component.scss'],
    providers: [Commfunc, CusOperationHttpService]
})
export class DownCenterComponent implements OnInit {
    code: any;
    first: number = 0;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    moreDis: boolean;
    downbtn: boolean;
    approveStatus: any;
    msgs: Message[] = [];
    showID: any;//记录id
    display: boolean = false;
    detailHeader = '下载详情';
    initiatedBeginDate: Date = null;      //发起日期起
    initiatedEndDate: Date = null;        //发起日期止
    approveBeginDate: Date = null;//审批完成时间起
    approveEndDate: Date = null;//审批完成时间止
    ch: any = CALENDAR_CN;
    temData: Date = new Date();
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    //全选/单选
    selectedAll: any = [];
    checkAll: boolean = false;
    checkBox: any = [];
    dataTable: any = [];
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    downloadRegQryBean: DownloadRegQryBean = new DownloadRegQryBean();
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public httpService: CusOperationHttpService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        this.codeValues();
        this.approveStatus = this.code['ApproveStatusDownload'];
    }
    ngOnInit() {
        this.downloadRegQryBean.pageSize = 10;
        this.downloadRegQryBean.pageNum = 1;
        this.query();
    }
    bigquery() {
        this.downloadRegQryBean.pageSize = 10;
        this.downloadRegQryBean.pageNum = 1;
        this.first = 0;
        this.query();
    }
    //发起时间
    showInitiated(data) {
        if (this.initiatedBeginDate && this.initiatedBeginDate != null && this.initiatedEndDate && this.initiatedEndDate != null) {
            if (this.commfunc.transDateN(this.initiatedEndDate) < this.commfunc.transDateN(this.initiatedBeginDate)) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '结束发起日期必输大于开始发起日期!' }];
                if (data == 1) {
                    this.initiatedBeginDate = null;
                } else if (data == 2) {
                    this.initiatedEndDate = null;
                }
                return;
            }
        }
    }
    //审批时间
    showApprove(data) {
        if (this.approveBeginDate && this.approveBeginDate != null && this.approveEndDate && this.approveEndDate != null) {
            if (this.commfunc.transDateN(this.approveEndDate) < this.commfunc.transDateN(this.approveBeginDate)) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '审批完成结束日期必须大于审批完成开始日期!' }];
                if (data == 1) {
                    this.approveBeginDate = null;
                } else if (data == 2) {
                    this.approveEndDate = null;
                }
                return;
            }
        }
    }
    query() {
        this.downloadRegQryBean.initiatedBeginDate = this.commfunc.transDateN(this.initiatedBeginDate);
        this.downloadRegQryBean.initiatedEndDate = this.commfunc.transDateN(this.initiatedEndDate);
        this.downloadRegQryBean.approveBeginDate = this.commfunc.transDateN(this.approveBeginDate);
        this.downloadRegQryBean.approveEndDate = this.commfunc.transDateN(this.approveEndDate);
        this.httpService.queryDownloadInfoProcess(this.downloadRegQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.pageList;
                this.dataTable.forEach(item => {
                    if (item.isDownload == '0') {
                        item.downbtn = false;
                    } else if (item.isDownload == '1') {
                        item.downbtn = true;
                    }
                });
                this.total = data.totalNum;
                this.showID = data.recordId;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    //全选按钮
    checkAllClick() {
        for (let i = 0; i < this.dataTable.length; i++) {
            if (this.checkAll == true) {
                this.dataTable[i].checkBox = true;
                this.selectedAll.push(this.dataTable[i]);
            }
            else {
                this.selectedAll = [];
                this.dataTable[i].checkBox = false;
            }
        }
    }

    //单选按钮
    checkBoxClick(col) {
        if (col.checkBox) {
            this.selectedAll.push(col)
        } else {
            this.selectedAll.forEach((item, i) => {
                if (item == col) {
                    this.selectedAll.splice(i, 1)
                }
            });
        }
        if (this.selectedAll.length == this.dataTable.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    //分页
    paginate(event) {
        this.downloadRegQryBean.pageSize = event.rows * 1;
        this.downloadRegQryBean.pageNum = event.page + 1;
        this.first = event.page * this.downloadRegQryBean.pageSize;
        this.query();
    }
    reset() {
        this.downloadRegQryBean.approveStatus = '';
        this.downloadRegQryBean.fileName = '';
        this.downloadRegQryBean.approveBeginDate = '';
        this.downloadRegQryBean.approveEndDate = '';
        this.downloadRegQryBean.initiatedBeginDate = '';
        this.downloadRegQryBean.initiatedEndDate = '';
        this.initiatedBeginDate = null;
        this.initiatedEndDate = null;
        this.approveBeginDate = null;
        this.approveEndDate = null;
    }
    toDetail(item) {
        if (item.downloadDate == null) {
            item.downloadDate = '-';
        }
        if (item.failureDate == null) {
            item.failureDate = '-';
        }
        this.router.navigate(["pages/tzb/custom/downloadCenter/downloadDetail", item])
    }
    jilu() {
        let list = []
        this.selectedAll.forEach(item => {
            list.push(item.fileId)
        });

    }
    down(temp) {
        let path = temp.downloadUrl;
        let donwname = temp.downloadName;
        let list = [];
        list.push(temp.fileId);
        let arr = {
            recordId: this.showID,
            downloadList: list,
        }
        this.httpService.downloadProcess(arr).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.query();
                open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(donwname) + "&fileUrl=" + path,'_blank');
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });

    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}