import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {DownloadDetailComponent} from './downloadDetail/downloadDetail.component';
import { DownCenterComponent } from "app/pages/tzb/custom/downloadCenter/downCenter/downCenter.component";

export const routes: Routes = [
    {
        path: '',
        children: [
           {
               path:'downloadDetail',
               component:DownloadDetailComponent
           },
           {
               path:'downCenter',
               component:DownCenterComponent
           }
            
        ]
    }
]
export const DownloadCenterRouting: ModuleWithProviders = RouterModule.forChild(routes);